.file {
    width: 90px;
    height: 90px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &:hover {
        background: white;
    }
    svg {
        width: 50px;
        height: 50px;
    }

    .fileName {
        font-size: 12px;
    }
}
